body {
  box-sizing: border-box;
  font-family: Courier New , Courier , monospace;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    margin: 50px 0;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-header input {
  border: none;
  border-bottom: 2px solid grey;
  margin: 1px 0;
  padding: 10px;
  width: 200px;
}

a {
  background-color: white;
  border: none;
  font-size: 13.33px;
  height: 15px;
  margin: 1px 0;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  width: 200px;
}

.App-header button {
  background-color: dodgerblue;
  border: none;
  color: white;
  margin: 1px 0;
  padding: 10px;
  transition: 1s;
  width: 220px;
}

.App-header button:disabled {
  background-color: lightgray;
  border: none;
  margin: 1px 0;
  padding: 10px;
  transition: 1s;
  width: 220px;
}

.App-header a {
  color: black;
}

.App-link {
  color: #61dafb;
}

@keyframes shake {

  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
